import React, {Component} from 'react';
import Introduction from '../../components/Introduction';
import Breadcrumb from '../../components/Breadcrumb';
import {connect} from 'react-redux';
import * as Actions from '../../actions';
import {Helmet} from 'react-helmet';
const loadJs = require('loadjs');
const moduleName = 'Giới thiệu';

class Index extends Component {
    componentDidMount() {
        loadJs('/js/main.js');
        this.props.fetchData();
    }

    render() {
        return (
            <div>
                <Helmet>
                    <title>{moduleName}</title>
                </Helmet>
                <Breadcrumb module={moduleName} title={this.props.data ? this.props.data.name : ''}/>
                <Introduction data={this.props.data} loading={this.props.fetching}/>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        fetching: state.payloadData.fetching,
        data: state.payloadData.data,
        error: state.payloadData.error
    };
};

const mapDispatchToProps = dispatch => {
    return {
        fetchData: () => dispatch(Actions.introductionAction.requestData())
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Index);